
.header{position:fixed;line-height:45px;left:0;top:0;right:0;width:100%;height:45px;background-color:#EE7419;color:#fff;z-index:80
}
.header .header-title{display:inline-block;margin-left:12.5%;font-size:20px
}
.header .header-title::before{display:inline-block;line-height:20px;text-align:center;content:'?';height:20px;width:20px;-webkit-border-radius:50%;border-radius:50%;color:#EE7419;background-color:#fff;margin-right:5px
}
.header .header-nav{display:inline-block;margin-left:4%
}
.content{width:1055px;margin:125px auto;background-color:#fff;-webkit-border-radius:5px;border-radius:5px;-webkit-box-shadow:0px 2px 6px rgba(0,0,0,0.5);box-shadow:0px 2px 6px rgba(0,0,0,0.5)
}
@media only screen and (max-width: 737px){
.content{width:90%
}
.content .manage{font-size:14px
}
.content .manage .list-header .item{font-size:16px;padding:5px 0
}
.content .manage .list-header .btn{width:75px;height:25px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer
}
.content .manage .list-content .text{padding:10px 0
}
.content .manage .list-content .text:last-child{font-size:0
}
.content .manage .list-content .text:nth-child(2){width:30%
}
.content .manage .list-content .text:nth-child(3){width:100px
}
.content .manage .list-foot .all-select{padding:10px 0
}
.content .manage .btn{width:65px;height:25px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:10px;margin:0
}
}



