
.edit{width:95%;margin:0 auto
}
.edit .edit-question-wrapper{padding:20px
}
.add-question{width:100%;text-align:center
}
.add-question .fold-enter-active,.add-question .fold-leave-active{-webkit-transition:all 0.8s;transition:all 0.8s;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
.add-question .fold-enter,.add-question .fold-leave-active{-webkit-transform:translate3d(0, 100%, 0);transform:translate3d(0, 100%, 0)
}
.add-question .select-type{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;padding:10px 0;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;height:50px;border:1px solid #d0d0d0;border-bottom:none;z-index:-1
}
.add-question .select-type .select-btn{width:100px;height:35px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:12px;background-color:#fff;margin-right:30px;line-height:35px;font-size:14px
}
.add-question .select-type .select-btn:hover{color:#fff;background-color:#EE7419
}
@media only screen and (max-width: 737px){
.add-question .select-type .select-btn{width:80px;height:35px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:12px;background-color:#fff;margin-right:10px
}
.add-question .select-type .select-btn:hover{color:#fff;background-color:#EE7419
}
}
.add-question .select-type .select-btn:last-child{margin:0
}
.add-question .select-type .select-btn i{margin-right:5px
}
.add-question .add-btn{padding:10px 0;line-height:70px;height:70px;background-color:#eee;border:1px solid #d0d0d0;border-top:none;cursor:pointer;-webkit-transform:translateZ(0);transform:translateZ(0);z-index:60
}
.add-question .add-btn span:first-child{display:inline-block;vertical-align:top;font-size:40px
}
.add-question .add-btn span:last-child{position:relative;top:2px;font-size:20px
}
.edit .question-content{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;position:relative;padding:15px 15px 30px 15px;margin-bottom:10px
}
.edit .question-content:hover{background-color:#fdf1e6
}
.edit .question-content:hover .control{visibility:visible
}
.edit .question-content:hover .sign,.edit .question-content:hover .title{font-weight:bold
}
.edit .question-content .sign{padding:8px 10px 0 5px
}
.edit .question-content .control{position:absolute;bottom:10px;right:0
}
.edit .edit-foot-wrapper{width:85%;margin:0 auto;-webkit-box-sizing:border-box;box-sizing:border-box;padding:30px 0 50px 0
}
.edit .edit-foot-wrapper .end-date{display:inline-block
}
.edit .edit-foot-wrapper .btn-wrapper{float:right;margin-right:70px
}
.edit .edit-foot-wrapper .btn-wrapper .btn{width:80px;height:30px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:12px;background-color:#fff;margin-right:10px
}
.edit .edit-foot-wrapper .btn-wrapper .btn:hover{color:#fff;background-color:#EE7419
}



