
.modal{position:fixed;left:0;top:0;right:0;bottom:0;background-color:rgba(0,0,0,0.4);z-index:88
}
.modal .modal-wrapper{width:350px;height:180px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;position:fixed;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%);top:40%;left:50%;border:1px solid #EFEFEF;background-color:#fff;-webkit-transition:all 1s;transition:all 1s;z-index:100px
}
.modal .modal-wrapper .modal-head{-webkit-box-sizing:border-box;box-sizing:border-box;padding:10px 20px;margin-bottom:20px;height:40px;width:100%;background-color:#EFEFEF;font-weight:bold
}
.modal .modal-wrapper .modal-head .close{float:right;position:relative;bottom:5px;font-size:24px;cursor:pointer
}
.modal .modal-wrapper .modal-body{padding:0px 20px;line-height:30px;height:60px;margin-bottom:20px
}
.modal .modal-wrapper .modal-foot{text-align:center;font-size:0
}
.modal .modal-wrapper .modal-foot .btn{width:70px;height:25px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:12px;background-color:#fff;margin-right:10px
}
.modal .modal-wrapper .modal-foot .btn:hover{color:#fff;background-color:#EE7419
}
.fade-enter-active,.fade-leave-active{-webkit-transition:all 0.5s;transition:all 0.5s
}
.fade-enter,.fade-leave-active{opacity:0
}



