
.manage{padding:10px
}
.manage .list{width:95%;margin:0 auto
}
.manage .list-header .item{padding:20px 0;font-size:18px;text-align:left
}
.manage .list-header .item:last-child{text-align:right
}
.manage .list-header .item:nth-child(3){text-align:center;padding-right:10px
}
.manage .list-header .btn{margin-left:15px;width:100px;background-color:#EE7419;color:#fff;cursor:pointer;font-weight:bold
}
.manage .list-content{border-bottom:1px solid #EFEFEF;margin:0 110px
}
.manage .list-content:hover{background-color:#FCF0e5
}
.manage .list-content.active{background-color:#FCF0e5
}
.manage .list-content .text{padding:10px 0
}
.manage .list-content .text:first-child{width:30%;text-align:left
}
.manage .list-content .text:nth-child(2){width:20%
}
.manage .list-content .text:nth-child(3){width:25%;text-align:center
}
.manage .list-content .text:last-child{text-align:right
}
.manage .list-content .text.release{color:#a4f47e
}
.manage .list-content .text.end{color:#ef2b2b
}
.manage .list-foot .all-select{padding:20px 0
}
.manage .btn{width:70px;height:30px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:12px;background-color:#fff;margin-right:10px
}
.manage .btn:hover{color:#fff;background-color:#EE7419
}
.manage .remove{text-align:center;padding:1%
}
.manage .rebuild{height:300px;line-height:300px;text-align:center
}
.manage .rebuild .btn-build{width:200px;height:50px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;color:#fff;background-color:#EE7419;font-size:25px;font-weight:bold;cursor:pointer
}



