
.calendar-wrapper{width:250px;height:250px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;position:absolute;font-size:14px;cursor:default;z-index:88
}
.calendar-wrapper .active{background:#ee7419;color:#fff;font-weight:bold
}
.calendar-wrapper .preview{color:#fff;text-align:center;height:35px;line-height:35px;position:relative;background-color:#e77408
}
.calendar-wrapper .preview .year-month{cursor:pointer
}
.calendar-wrapper .preview .left-triangle{position:absolute;top:10px;left:15px;width:0;height:0;border-top:6px solid transparent;border-right:8px solid #fff;border-bottom:6px solid transparent;cursor:pointer
}
.calendar-wrapper .preview .right-triangle{position:absolute;top:10px;right:15px;width:0;height:0;border-top:5px solid transparent;border-left:8px solid #fff;border-bottom:5px solid transparent;cursor:pointer
}
.calendar-wrapper .date-list{text-align:center;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-flex-wrap:wrap;-ms-flex-wrap:wrap;flex-wrap:wrap;background:#fff
}
.calendar-wrapper .date-list .week-wrapper{margin-top:-1px;height:20px;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;background-color:#e77408;color:#fff
}
.calendar-wrapper .date-list .week-wrapper .week{width:35.71429px
}
.calendar-wrapper .date-list .date{width:35.61429px;height:32.5px;line-height:32.5px;cursor:pointer
}
.calendar-wrapper .date-list .date:hover{background-color:rgba(238,116,25,0.8);color:#fff;font-weight:bold
}
.calendar-wrapper .date-list .date.noNow{color:#eee
}
.calendar-wrapper .month-list{text-align:center;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-flex-wrap:wrap;-ms-flex-wrap:wrap;flex-wrap:wrap;background:#fff
}
.calendar-wrapper .month-list .month{line-height:71.66667px;width:62.5px;height:71.66667px;cursor:pointer
}
.calendar-wrapper .month-list .month:hover{background-color:rgba(238,116,25,0.8);color:#fff;font-weight:bold
}
.calendar{display:inline-block;position:relative
}
.calendar .message{font-size:14px;width:250px;height:30px;text-indent:10px;cursor:pointer
}
.calendar .move-enter-active,.calendar .move-leave-active{-webkit-transition:all 0.5s;transition:all 0.5s
}
.calendar .move-enter,.calendar .move-leave-active{-webkit-transform:translateY(30px);-ms-transform:translateY(30px);transform:translateY(30px);opacity:0.5
}
.calendar .disappear{position:fixed;width:100%;height:100%;top:0;left:0;right:0;z-index:20
}



