
.topic{position:relative;width:100%
}
.topic .title{margin-bottom:2px;line-height:24px
}
.topic .title .text{padding:5px 5px 5px 2px
}
.topic .title .editing{padding:3px 3px 3px 0
}
.topic .icon-radio,.topic .icon-checkbox,.topic .icon-checkbox_fill{font-size:14px
}
.select-type .edit-text{display:inline-block;line-height:24px
}
.select-type .edit-text .text{padding:2px 5px 2px 3px
}
.select-type .edit-text,.select-type .edit-text .editing{padding:0 3px 0 3px
}
.select-type .remove{font-size:20px;cursor:pointer;color:red
}
.select-type .remove:hover{opacity:0.6
}
.select-type .add-option:hover .inner{visibility:visible
}
.select-type .add-option .inner{text-align:center;font-size:30px;border:1px dashed;visibility:hidden;cursor:pointer
}
.text-type .textarea{margin-top:10px;height:100px;width:400px;border:1px solid #a5a5a5
}
@media only screen and (max-width: 737px){
.text-type .textarea{width:200px
}
}
.text-type .select-flag{position:absolute;top:10px;right:0;cursor:pointer
}



