
.view{width:95%;margin:0 auto
}
.view-wrapper{padding:20px
}
.view-wrapper .view-chart{width:100%;height:400px;padding:15px;margin-bottom:10px
}
@media only screen and (max-width: 737px){
.view-wrapper{padding:20px 0
}
.view-wrapper .view-chart{padding:15px 0
}
}
.view-foot{padding:30px 0 50px 0;text-align:center
}
.view-foot .btn{width:70px;height:30px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:12px;background-color:#fff
}
.view-foot .btn:hover{color:#fff;background-color:#EE7419
}



