
.fill{width:95%;margin:0 auto
}
.fill-wrapper{padding:20px
}
.fill-wrapper .fill-content{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;position:relative;padding:15px;margin-bottom:10px
}
.fill-wrapper .fill-content:hover{background-color:#fdf1e6
}
.fill-wrapper .fill-content:hover .sign,.fill-wrapper .fill-content:hover .option-title{font-weight:bold
}
.fill-wrapper .fill-content .sign{padding:8px 10px 0 5px
}
.fill-wrapper .fill-content .option-title{display:inline-block;line-height:24px;padding:5px 5px 5px 2px;margin-bottom:5px
}
.fill-wrapper .fill-content .type-text{display:block;margin-top:10px;width:400px;height:100px;resize:none
}
@media only screen and (max-width: 737px){
.fill-wrapper .fill-content .type-text{width:200px
}
}
.fill-wrapper .fill-content .fill-flag{position:absolute;top:20px;right:20px
}
.fill-wrapper .fill-content .question-option{padding:6px
}
.fill-wrapper .fill-content .question-option input[type='radio'],.fill-wrapper .fill-content .question-option input[type='checkbox']{display:none
}
.fill-wrapper .fill-content .question-option label{position:relative;display:inline-block;cursor:pointer;padding-left:25px
}
.fill-wrapper .fill-content .question-option label:before{content:'';position:absolute;bottom:0;left:0;width:14px;height:14px;background-color:#e0e0e0
}
.fill-wrapper .fill-content .question-option input[type='radio']+label:before{-webkit-border-radius:50%;border-radius:50%
}
.fill-wrapper .fill-content .question-option input[type='checkbox']+label:before{-webkit-border-radius:3px;border-radius:3px
}
.fill-wrapper .fill-content .question-option input[type='radio']:checked+label:before{content:"\2022";color:#fff;font-size:20px;text-align:center;line-height:13px;background-color:#ee7419
}
.fill-wrapper .fill-content .question-option input[type='checkbox']:checked+label:before{content:"\2713";color:#fff;font-size:13px;text-align:center;background-color:#ee7419
}
.fill-foot{padding:30px 0 50px 0;text-align:center
}
.fill-foot .btn{width:70px;height:30px;border:1px solid rgba(0,0,0,0.5);-webkit-border-radius:3px;border-radius:3px;-webkit-box-shadow:0 1px 2.5px rgba(0,0,0,0.5);box-shadow:0 1px 2.5px rgba(0,0,0,0.5);cursor:pointer;font-size:12px;background-color:#fff
}
.fill-foot .btn:hover{color:#fff;background-color:#EE7419
}



